* {
  margin: 0;
  box-sizing: border-box;
  align-items: center;
}
.header {
  background-color: black;
  height: 50px;
}
.menu {
  height: 50px;
}

.cybersoft-footer {
  background: #d9d9d9;
  padding: 20px 0;
}

.show-name {
  font-family: "Inter";
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
}
.short-desc {
  font-family: "Inter";
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
  color: #cbc9c9;
}
.card-footer {
  padding: 0;
  display: flex;
}
.btn-size{
  padding: 1px 6px;
  border: 2px solid transparent;
}
.btn-choice{
  border: 2px solid #6200EE;
}
.btn-size:hover{
  border: 2px solid #6200EE;
}
.buy-now {
  height: 54px;
  border: none;
  // background: #e1b067;
  // padding: 10px 30px;
  flex: 1;
  margin: 0;
}
.shoe-price {
  border: none;
  height: 54px;
  background: #dedddc;
  padding: 5px 15px;
  flex: 1;
}
.product-future {
  max-width: 720px;
  height: 74px;
  background: linear-gradient(180deg, #f21299 0%, #1b02b5 100%);
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 48px;
  padding-left: 30px;
  padding-top: 10px;
}

.btn-wrapper {
  color: #cccccc;
}
.btnCong {
  color: #fff;
  border: none;
  margin-right: 10px;
  padding: 5px 12px;
  background: linear-gradient(180deg, #6181f3 0%, #7c97f5 99.48%);
}
.btnTru {
  border: none;
  color: #fff;
  margin-left: 10px;
  padding: 5px 14px;
  background: linear-gradient(180deg, #6181f3 0%, #7c97f5 99.48%);
}

.addtocart {
  border: none;
  display: block;
  margin-top: 10px;
  padding: 5px 12px;
  background: linear-gradient(
    270deg,
    rgba(62, 32, 248, 0.9) 5.14%,
    #d017ee 89.71%
  );
}
.product-title {
  font-family: "Inter";
  font-style: normal;
  font-weight: 300;
  font-size: 30px;
  line-height: 36px;
}
.addtocart {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  color: #fff8f8;
  width: 175px;
  height: 64px;
}
.margin-left {
  margin-left: 30px;
}
.marginBlock{
  margin: 0 20px;
}



// Login
.login {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 48px;
}
.button-right {
  position: relative;
  right: -308px;
  padding: 10px 30px;
  border-radius: 20px;
  background-color: #6200ee;
}
.button-right:hover {
  background-color: #6200ee;
  opacity: 0.9;
}
.background-primary {
  margin: 30px;
  background: #1877f2;
  height: 63px;
}
.login-text {
  display: inline-block;
  background: #6200ee;
  margin-left: 50px;
  padding: 10px 30px;
  box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.14), 0px 1px 10px rgba(0, 0, 0, 0.12),
    0px 2px 4px rgba(0, 0, 0, 0.2);
}
.login-text:hover {
  background: #6200ee;
  opacity: 0.9;
}
.register-text {
  display: inline-block;
  text-decoration: none;
  color: rgba(21, 42, 235, 0.8);
}
.facebook-block {
  background-color: #1877f2;
  height: 63px;
  border-radius: 21px;
  text-decoration: none;
}
.fa-facebook {
  font-size: 41px;
  color: #fff;
  margin-right: 20px;
}
.facebook-text {
  color: #fff;
  font-size: 20px;
}

