@media only screen and (max-width: 576px) {
  /* For mobile phones: */
  // .header{
  //   height: 80px;
  // }
  .col-sm-fix{
    position: relative;
    top: 50px;
    right: 60px;
  }
  .font-size{
    font-size: 12px;
  }
  .justify-content-around{
    justify-content: center;
  }
  .btn-size{
    padding: 0 3px;
    margin: 0 6px;
  }
}
@media only screen and (max-width: 768px) {
  
  .col-sm-fix{
    position: relative;
    top: 50px;
    right: 60px;
  }
}
@media screen and (max-width: 960px) {
  .hide-info {
    display: none;
  }
  .img-full {
    width: 100%;
  }
  .flex-wrap {
    display: flex;
    flex-wrap: wrap !important;
  }
  .margin-shoe {
    margin-bottom: -20px;
    margin-top: -20px;
  }

}
.flex-direction{
  flex-direction: column;
}


